@import url(https://fonts.googleapis.com/css?family=Roboto:100);
.MainContent_loading__xXdDZ {
  display: inline-block;
  width: 55px;
  height: 55px;
  margin: 50px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 3px solid rgba(255,255,255,.3);
  border-radius: 50%;
  border-top-color: rgb(0, 0, 0);
  animation: MainContent_spin__2SMJF 1s ease-in-out infinite;
